import React , { Component } from 'react';
import ProfileItem from "../components/ProfileItem";
import * as actions from "../store/actions/userList";
import { connect } from 'react-redux'
import Hoc from "../hoc/hoc";
import { MDBRow , MDBCol , MDBFormInline , MDBIcon  } from "mdbreact";


class ProfileList extends Component {
    componentDidMount () {
        this.props.userList ()
    }


    render () {
        const {users} = this.props
        const {loading} = this.props
        console.log (users)
        console.log (loading)
        if (this.props.users === null || this.props.users === undefined) {
            return (
                <section className="section-preview d-flex justify-content-between" >
                <div className="preloader-wrapper big active" >
                    <div className="spinner-layer spinner-blue-only">
                        <div className="circle-clipper left" >
                            <div className="circle" ></div>
                        </div>
                        <div className="gap-patch" >
                            <div className="circle"></div>
                        </div>
                        <div className="circle-clipper right" >
                            <div className="circle" ></div>
                        </div>
                    </div>
                </div>
            </section>)
        } else {
            return (
                <Hoc>
                    <MDBCol md="12" lg='12'>
                        <MDBFormInline className="md-form">
                            <MDBIcon icon="search"/>
                            <input className="form-control form-control-sm ml-3 w-75" type="text"
                                   placeholder="Search"
                                   aria-label="Search"/>
                        </MDBFormInline>
                    </MDBCol>

                    <div className='container-fluid'>
                        <section className="text-center my-5">
                            <h2 className="h1-responsive font-weight-bold my-5">
                                Portfolios
                            </h2>
                            <p className="grey-text w-responsive mx-auto mb-5">
                                Get the best portfolio from our web by creating here.
                            </p>
                            <MDBRow className="d-flex justify-content-center">
                                <Hoc>
                                    {this.props.users.user.map ((user , index) => {
                                        return <ProfileItem key={index}
                                                            username={user.username}
                                                            first_name={user.first_name}
                                                            last_name={user.last_name}
                                                            about={user.profile.about}
                                                            profile_pics={user.profile.profile_pics}
                                                            id={user.id}

                                        />
                                    })}
                                </Hoc>
                            </MDBRow>
                        </section>
                    </div>
                </Hoc>
            )
        }
    }
}

const mapStateToProps = (state) => {
    return {
        users: state.users.users ,
        loading: state.users.loading ,
        token: state.auth.token
    }
}

const mapDispatchToProps = dispatch => {
    return {
        userList: () => dispatch (actions.usersList ())
    }
}


export default connect (mapStateToProps , mapDispatchToProps) (ProfileList);
