import React , { Component } from 'react';
import { usersDetail } from "../store/actions/userDetail";
import { projectUrlAnonymous }   from "../store/actions/projectUrl";
import { connect } from 'react-redux'
import Hoc from "../hoc/hoc";
import { MDBRow } from "mdbreact";
import UserContactPage from "../components/UserContactPage";
import PortfolioStarter from '../components/PortfolioStarter'


class ProfileDetail extends Component {
    componentDidMount () {
        this.props.userDetail (this.props.match.params.id)
    }


    UNSAFE_componentWillReceiveProps (newProps) {
        if (newProps.user_detail !== this.props.user_detail) {
            if (newProps.user_detail !== undefined && newProps.user_detail !== null) {
                this.props.projectUrlAnonymous (newProps.user_detail.username);
            }
        }
    }


    render () {
        const {user_detail} = this.props
        const {user_detail_loading} = this.props
        console.log ('user_detail for' , this.props)
        if (this.props.user_detail === null || this.props.user_detail === undefined) {
            return null
        } else {
            const {
                last_name ,
                first_name ,
                email ,
                linkedin ,
                twitter ,
                instagram ,
                phone_number ,
                background_image ,
                about
            } = this.props.user_detail
            const {profile_pics} = this.props.user_detail.profile
            return (
                <Hoc className='bg-light'>
                    <PortfolioStarter background_image={background_image}
                                      about={about}
                                      profile_pics={profile_pics}/>
                    <div className="container ml-2 mr-2 mx-auto">
                        <UserContactPage first_name={first_name}
                                         last_name={last_name}
                                         twitter={twitter}
                                         instagram={instagram}
                                         phone_number={phone_number}
                                         email={email}/>
                    </div>
                </Hoc>
            )
        }
    }
}

// 08066961763

const mapStateToProps = (state) => {
    return {
        // user detail section
        user_detail: state.user_detail.user_detail ,
        user_detail_loading: state.user_detail.loading ,
        token: state.auth.token,

        // project url section
        // projectUrl:state.projectUrl.projectUrl,
        // projectUrl_loading:state.projectUrl.loading,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        userDetail: (id) => dispatch (usersDetail (id)),
        projectUrlAnonymous: (username) => dispatch (projectUrlAnonymous (username))
    }
}


export default connect (mapStateToProps , mapDispatchToProps) (ProfileDetail);
