import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import registerServiceWorker from './registerServiceWorker';
import { createStore, compose, applyMiddleware,combineReducers } from 'redux';
import { Provider } from 'react-redux';
import thunk from 'redux-thunk';

import '@fortawesome/fontawesome-free/css/all.min.css';
import '@fortawesome/react-fontawesome';
import 'bootstrap/dist/css/bootstrap.css'
import 'mdbreact/dist/css/mdb.css';

import authReducer from './store/reducers/auth';
import userListReducer from './store/reducers/usersList';
import userDetailReducer from './store/reducers/userDetail';
import projectUrlReducer from './store/reducers/projectUrl';
import projectUrlCreateReducer from './store/reducers/projectUrl';

const composeEnhances = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose


const rootReducer = combineReducers({
    auth:authReducer,
    users:userListReducer,
    user_detail:userDetailReducer,
    project_url:projectUrlReducer,
    project_url_create:projectUrlCreateReducer,

})

const store = createStore(
    rootReducer
    , composeEnhances(
    applyMiddleware(thunk)
));

const app = (
    <Provider store={store}>
        <App />
    </Provider>
)

ReactDOM.render(app, document.getElementById('root'));
registerServiceWorker();