import React from "react";
import { MDBCarousel ,
  MDBCarouselCaption ,
  MDBCarouselInner ,
  MDBCarouselItem , MDBView , MDBMask , MDBContainer} from
        "mdbreact";
import image1 from '../images/image1.jpeg'
import image2 from '../images/image2.jpeg'
import image3 from '../images/image3.jpeg'
const CarouselPage = () => {
    return (
        <React.Fragment>
            <MDBCarousel
                activeItem={1}
                length={3}
                showControls={true}
                showIndicators={true}
                className="z-depth-1"
            >
                <MDBCarouselInner>
                    <MDBCarouselItem itemId="1">
                        <MDBView>
                            <img
                                className="d-block w-100"
                                src={`${image1}`}
                                alt="First slide"
                            />
                            <MDBMask overlay="black-strong"/>
                        </MDBView>
                        <MDBCarouselCaption>
                            <h3 className="h3-responsive">Light mask</h3>
                            <p>Create Awesome PortFolio</p>
                        </MDBCarouselCaption>
                    </MDBCarouselItem>
                    <MDBCarouselItem itemId="2">
                        <MDBView>
                            <img
                                className="d-block w-100"
                                src={`${image2}`}
                                alt="Second slide"
                            />
                            <MDBMask overlay="black-strong"/>
                        </MDBView>
                        <MDBCarouselCaption>
                            <h3 className="h3-responsive">Strong mask</h3>
                            <p>Second text</p>
                        </MDBCarouselCaption>
                    </MDBCarouselItem>
                    <MDBCarouselItem itemId="3">
                        <MDBView>
                            <img
                                className="d-block w-100"
                                src={`${image3}`}
                                alt="Third slide"
                            />
                            <MDBMask overlay="black-strong"/>
                        </MDBView>
                        <MDBCarouselCaption>
                            <h3 className="h3-responsive">Slight Mast</h3>
                            <p>Third text</p>
                        </MDBCarouselCaption>
                    </MDBCarouselItem>
                </MDBCarouselInner>
            </MDBCarousel>
        </React.Fragment>
    );
}

export default CarouselPage;